@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 100;
}

*{
  box-sizing:border-box;
}

a {
  color: #8C8987;
}

main {
  min-height: 100vh;
  max-width: 100vw;
  display: grid;
  place-items: center;
  padding:20vh;
}

main.project-details{
  padding:20vh 0;
}

button, .button {
  font-size:1rem;
  background-color: black;
  color:white;
  min-width:6rem;
  padding:.6rem;
  display: block;
  cursor: pointer;
}

.edit-icons {
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
  cursor: pointer;
  margin-left: 1rem;
  color: #8C8987;
}



.button {
  background: white;
  border: 1px solid #111;
  color: #111;
  padding:.4rem;
  max-width:6rem;
  text-align: center;
  margin-top: 1rem;
}
.project {
    color:#8C8987;
    width:100%;
    min-height: 600px;
    position: relative;
    cursor: pointer;

    overflow:hidden;
}

.project .bilde{
    min-height:600px;
    width:100%;
}

.project .bilde img{
    width:100%;
    min-height:600px;
    object-fit: cover;
    margin:0;
    padding:0;
    -webkit-transform:scale(1.3);
            transform:scale(1.3);
}
.project:hover > .bilde img{
    -webkit-transition:20s ease;
    transition:20s ease;
    -webkit-transform:scale(1.1);
            transform:scale(1.1);
}


.project .project-title{
    position:absolute;
    left:2rem;
    bottom:2rem;
    font-size:2rem;
    color: white;
    font-weight: 600;
}

.admin-icons{
    position:absolute;
    bottom:2rem;
    right:2rem;
    color: #8C8987;
}

@-webkit-keyframes zoomIn{
    from{
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    to{
        -webkit-transform:scale(100vw,100vh);
                transform:scale(100vw,100vh);
    }
}

@keyframes zoomIn{
    from{
        -webkit-transform: scale(0,0);
                transform: scale(0,0);
    }
    to{
        -webkit-transform:scale(100vw,100vh);
                transform:scale(100vw,100vh);
    }
}
main.projects{
    padding:12vh 1rem;
  }

.projectsContainer {
    display:grid;
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
    grid-gap:.6rem;
    gap:.6rem;   
}

.edit-icons-add {
    -webkit-transform:scale(10);
            transform:scale(10);
    fill:rgba(0,0,0,.8);
    cursor: pointer;
    margin-left: 1rem;
    margin-bottom:2rem;
    position: fixed;
    bottom: 4rem;
    right: 4rem;
    z-index:2;
  }
  

@media (max-width: 600px){
    .projectsContainer {
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); 
    }
}
main.project-details {
    position: relative;
    justify-content: center;
    text-align: justify;
    line-height: 1.9;
    margin-bottom: 30px;
    word-break: break-word;
}

.description p, .description a, .project-details .year{
    width: 35%;
    display:block;
    margin: auto;
    font-size: 18px;
    max-width: 360px;
} 


.project-details .description img{
    width:100%;
} 

.project-details h4{
    text-align: center;
} 

.project-details h6{
    width: 35%;
    display:block;
    margin: auto;
    max-width: 360px;
} 

.project-details .description a{
    padding-top: 3rem;
    width:10%;
} 

.pager {
    
    width:80vw;
    left:0;
    bottom: 5vh;
    height: 10vh;
    display:grid;
    grid-auto-flow: column;
    grid-gap:4rem;
    gap:4rem;
    align-items: center;
    justify-content: space-between;
    z-index: 1;



}

main.about{
    display:grid;
    grid-template-columns:1fr 1fr;
    grid-gap:1rem;
    gap:1rem;
    padding:20vh 5vh 5vh 5vh;
    align-items:start;
    height:100vh;
    overflow:hidden;
}

.profile-text{
    max-width:100%;
   
}


.about h1{
    font-size: 10rem;
    padding:0;
    margin:0;
    color: goldenrod;
}
.about h1 span{
    display:block;
}

.about p{
    font-size: 20px;
    font-weight: 100;
    font-family: 'Josefin Sans', sans-serif;   
    word-break: break-word;
    text-align: justify;
    line-height: 1.6; 
    width: 90%;
}

.about img {
    width:100%;
    max-height:100%;
    height:100%;
    object-fit:cover;
}

@media(max-width:1000px){
    .about h1{
        font-size: 4rem;
    }    
}


@media(max-width:600px){
    main.about{
        display:grid;
        grid-template-columns:1fr;
        grid-template-rows: 40vh 40vh;
        padding:10vh 0 10vh 0;
    }
    .about .profile-text {
        grid-row-start: 2;
    }
}



.header-container {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: #F2F2F2;
    display:grid;
    place-items: center;
    z-index: 3;
}

.burger{
    display: none;
    z-index: 4;
    position: absolute;
}

header {
    position: fixed;
    width: 100vw;
    height: 10vh;
    background: #F2F2F2;
    display:grid;
    grid-auto-flow: column;
    justify-content: start;
    align-items: center;
    grid-gap:1rem;
    gap:1rem;
    z-index: 3;
}

header a {
    text-decoration: none;
    color: #8C8987;
    margin-left: 15px;
}

[aria-current='page'], .active {
    font-weight: bold;
}

header p{
    margin-left: 2rem;
    margin-right: 2rem;
    color: #8C8987;

}

@media (max-width: 1000px){
    header{
        width:50%;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
        grid-auto-flow: row;
        align-content: center;
        justify-content: center;
        min-height: 100vh;
        width: 100vw;
        background:#f2f2f2;
    }
    .navn{
        position: absolute;
        top: 5vh;
        left: 50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }


    .header a {
        font-size: 2rem;
    }

    header.visible{
        -webkit-transform:translateY(0);
                transform:translateY(0);
        -webkit-transition: 1s ease;
        transition: 1s ease;
    }

    .burger{
        display:block;
        cursor: pointer;
        position: absolute;
        left: 0;
        top: 10;
        margin-left: 20px;
    }

    main {
        padding: 20vh 5vh;
    }

}


main.login {
    justify-items: left;
}

.login img {
    width:100px;
    margin-bottom:1rem ;
}
input, textarea, label {
    font-size:1rem;
    padding:.5rem;
    display:block;
    margin-bottom:1rem;
}

textarea {
    min-width: 60vw;
    min-height: 16rem;
}

.edit img {
    width:5rem;
 
}

.edit label {
    padding: 0;
}


